<template>
	<view v-if="onshow">
		<view class="tab-nav flexBox between">
			<view class="nav_item" :class="active==0 ? 'hot':''" @click="navClick(0)">快递</view>
			<view class="nav_item" v-if="data.is_verify" :class="active==1 ? 'hot':''" @click="navClick(1)">{{list.pickuptext||'上门自提'}}</view>
		</view>
		<!-- 地址 -->
		<view class="order_info" v-if="active==0">
			<navigator url="/pages/address/address?source=1" class="address-section">
				<view class="order-content" v-if="addressData.id != 0">
					<text class="yticon icon-shouhuodizhi"></text>
					<view class="cen">
						<view class="top">
							<text class="name">{{addressData.realname}}</text>
							<text class="mobile">{{addressData.mobile}}</text>
						</view>
						<text class="address">{{addressData.address}} {{addressData.area}}</text>
					</view>
					<text class="yticon icon-you"></text>
				</view>
				<view class="order-content" v-else>
				<text class="yticon icon-shouhuodizhi"></text>暂无收货地址 请添加
				<view class="cen"></view>
				<text class="yticon icon-you"></text></view>
				<image class="a-bg" src="data:image/png;base64,"></image>
			</navigator>
			<view class="goods-section">
				<view class="g-header b-b">
					<image class="logo" :src="sysset.shoplogo"></image>
					<text class="name">{{sysset.shopname}}</text>
				</view>
				<!-- 商品列表 -->
				<view class="g-item" >
					<image :src="data.goods.thumb"></image>
					<view class="right">
						<text class="title clamp">{{data.goods.title}}</text>
						<text class="spec">{{data.goods.spec_name}}</text>
						<view class="price-box">
							<text class="price">￥{{data.goods.price}}/{{data.goods.goods_num}}{{data.goods.units}}</span></text>
							<text class="number">x 1</text>
						</view>
					</view>
				</view>
			</view>
			
			<!-- 优惠明细 -->
			<view class="yt-list" >
				<view class="yt-list-cell b-b" v-if="data.headsmoney>0">
					<view class="cell-icon hb">
						减
					</view>
					<text class="cell-tit clamp">团长优惠</text>
					<text class="cell-tip " >-¥{{data.headsmoney}}</text>
				</view>
			</view>
			<!-- 金额明细 -->
			<view class="yt-list">
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp">商品金额</text>
					<text class="cell-tip">￥{{data.goods.price}}</text>
				</view>
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp">优惠金额</text>
					<text class="cell-tip red">-￥{{data.headsmoney}}</text>
				</view>
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp">运费</text>
					<text class="cell-tip">{{data.freight}}</text>
				</view>
				<view class="yt-list-cell desc-cell">
					<text class="cell-tit clamp">买家留言</text>
					<input class="desc" type="text" v-model="desc" placeholder="请填写备注信息" placeholder-class="placeholder" />
				</view>
			</view>
		</view>
		
		<view class="order_info" v-if="active==1">
			<navigator url="/pages/address/address?source=1" class="address-section">
				<view class="order-content" v-if="data.stores" v-for="(item,index) in data.stores">
					<text class="yticon icon-shouhuodizhi"></text>
					<view class="cen">
						<text class="address">{{item.storename}} {{item.address}}</text>
					</view>
					<text class="yticon icon-you"></text>
				</view>
				<view class="order-content" v-else>
				<text class="yticon icon-shouhuodizhi"></text>暂无选择 请添加
				<view class="cen"></view>
				<text class="yticon icon-you"></text></view>
				<image class="a-bg" src="data:image/png;base64,"></image>
			</navigator>
			<view class="goods-section">
				<view class="g-header b-b">
					<image class="logo" :src="sysset.shoplogo"></image>
					<text class="name">{{sysset.shopname}}</text>
				</view>
				<!-- 商品列表 -->
				<view class="g-item" >
					<image :src="data.goods.thumb"></image>
					<view class="right">
						<text class="title clamp">{{data.goods.title}}</text>
						<text class="spec">{{data.goods.spec_name}}</text>
						<view class="price-box">
							<text class="price">￥{{data.goods.price}}/{{data.goods.goods_num}}{{data.goods.units}}</span></text>
							<text class="number">x 1</text>
						</view>
					</view>
				</view>
			</view>
			<!-- 优惠明细 -->
			<view class="yt-list" >
				<view class="yt-list-cell b-b" v-if="data.headsmoney>0">
					<view class="cell-icon hb">
						减
					</view>
					<text class="cell-tit clamp">团长优惠</text>
					<text class="cell-tip " >-¥{{data.headsmoney}}</text>
				</view>
			</view>
			<!-- 金额明细 -->
			<view class="yt-list">
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp">商品金额</text>
					<text class="cell-tip">￥{{data.goods.price}}</text>
				</view>
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp">优惠金额</text>
					<text class="cell-tip red">-￥{{data.headsmoney}}</text>
				</view>
				<view class="yt-list-cell desc-cell">
					<text class="cell-tit clamp">买家留言</text>
					<input class="desc" type="text" v-model="desc" placeholder="请填写备注信息" placeholder-class="placeholder" />
				</view>
			</view>
		</view>
		<!-- 底部 -->
		<view class="footer">
			<view class="price-content">
				<text>实付款</text>
				<text class="price-tip">￥</text>
				<text class="price">{{data.price}}</text>
			</view>
			<text class="submit" @click="submit">提交订单</text>
		</view>
	</view>
</template>

<script>
	import {pintuanCreadOrder, orderSubmit,pintuanSumbitOrder} from "@/api/public";
	import {getUser} from "@/api/user";
	import js from "@/utils/jquery";
	import { VUE_APP_API_URL } from "@/utils/index";
	export default {
		data() {
			return {
				active:0,
				onshow:0,
				maskState: 0, //优惠券面板显示状态
				desc: '', //备注
				payType: 1, //1微信 2支付宝
				data: {
					dispatchtype: 0,
					remark: ""
				},
				addressData: {
					realname: '',
					mobile: '',
					addressName: '',
					address: '',
					area: '',
					default: false,
					id:0,
					lng:'',
					lat:''
				},
				diyform:{
				},
				list:[],
				alldownprice:0,
				merchid:0,
			}
		},
		onLoad(option){
			//获取商品
			let that = this;
			that.id = option.id;
			that.options = option;
			that.optionid = option.optionid,
			that.ladder_id = option.ladder_id,
			that.type = option.type,
			that.heads = option.heads,
			that.teamid = option.teamid;
			that.createOrder(option.id, option.optionid, option.ladder_id, option.type, option.heads, option.teamid);
		},
		methods: {
			navClick(e){
				this.active=e;
			},
			createOrder(id, optionid, ladder_id, type, heads, teamid){
				let that = this,s = [];
				pintuanCreadOrder(id, optionid, ladder_id, type, heads, teamid).then(res => {
					that.onshow = 1;
					that.data = res.data,
					that.sysset = res.sysset;
					if(res.data.address){
						that.aid = res.data.address.id
						that.addressData = {
							realname: res.data.address.realname,
							mobile: res.data.address.mobile,
							addressName: res.data.address.province + res.data.address.city + res.data.address.area + res.data.address.street,
							address: res.data.address.province + res.data.address.city + res.data.address.area + res.data.address.street,
							area: res.data.address.address,
							default: res.data.address.isdefault=="1"?true:false,
							id: res.data.address.id,
							lng: res.data.address.lat,
							lat: res.data.address.lng
						}
					}
					if(res.data.fields.length > 0) {
						that.diyform = {
							f_data : res.data.f_data,
							fields : res.data.fields
						}
					}
				}).catch(res=> {
					setTimeout(function(){
						uni.redirectTo({
							url:'/pages/mygroups/groupsorder?state=0'
						})
					},300)
					return false;
				})
				getUser().then(res =>{
								
					  that.userInfo = res;
				})
			},
			//显示优惠券面板
			toggleMask(type){
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(()=>{
					this.maskState = state;
				}, timer)
			},
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type){
				this.payType = type;
			},
			submit(){
				let t = this;
				let data = {
					id: t.options.id ? t.options.id : 0,
					group_option_id: t.options.optionid, 
					ladder_id: t.options.ladder_id,
					type: t.options.type,
					heads: t.options.heads,
					teamid:  t.options.teamid ? t.options.teamid : 0,
					aid: t.addressData.id ? t.addressData.id : 0,
					message: t.desc,
					realname: t.addressData.realname?t.addressData.realname:t.userInfo.nickname,
					mobile: t.addressData.mobile? t.addressData.mobile:t.userInfo.mobile,
					deduct:0,
					isverify:t.active,
				}
				data = t.convertModelToFormData(data);
				pintuanSumbitOrder(data).then(res => {
					
					if(res.error != 0){
						this.$api.msg(res.message);
						return false;
					}
					uni.redirectTo({
						url: `/pages/money/grouppay?id=${res.orderid}`
					})
				})
			},
			convertModelToFormData(val, formData = new Object(), namespace = '') {
				if((typeof val !== 'undefined') && (val !== null)) {
					if(val instanceof Date) {
						formData[namespace] = val.toISOString();
					//	Object.assign(formData,{ namespace:val.toISOString()})
						//formData.append(namespace, val.toISOString());
					} else if(val instanceof Array) {
						for(let i = 0; i < val.length ; i++){
							this.convertModelToFormData(val[i], formData, namespace + '['+i+']');
						}
					} else if(typeof val === 'object') {
						for (let propertyName in val) {
							if(val.hasOwnProperty(propertyName)) {
								this.convertModelToFormData(val[propertyName], formData, namespace ? namespace + '[' + propertyName + ']' : propertyName);
							}
						}
					} else {
						formData[namespace] = val.toString();
					//	Object.assign(formData,{namespace:val.toString()})
						//formData.append(namespace, val.toString());
					}
				}
				return formData;
			},
			dataChange: function(t) {
				var e = this.data, a = this.list;
				switch (t.target.id) {
				  case "remark":
					e.remark = t.detail.value;
					break;

				  case "deduct":
					if (e.deduct = t.detail.value, e.deduct2) return;
					var i = parseFloat(a.realprice);
					i += e.deduct ? -parseFloat(a.deductmoney) : parseFloat(a.deductmoney), a.realprice = i;
					break;

				  case "deduct2":
					if (e.deduct2 = t.detail.value, e.deduct) return;
					i = parseFloat(a.realprice);
					i += e.deduct2 ? -parseFloat(a.deductcredit2) : parseFloat(a.deductcredit2), a.realprice = i;
				}
				a.realprice <= 0 && (a.realprice = 1e-6), a.realprice = r.toFixed(a.realprice, 2), 
				this.$set(this,'data',e);
				this.$set(this,'list',a);
			},
			stopPrevent(){},
		}
	}
</script>

<style lang="scss">
	.tab-nav .nav_item.hot{background:linear-gradient(bottom,#ff5722,#fd823f);color:#fff;}
	page {
		background: $page-color-base;
		padding-bottom: 100upx;
	}

	.address-section {
		padding: 30upx 0;
		background: #fff;
		position: relative;

		.order-content {
			display: flex;
			align-items: center;
		}

		.icon-shouhuodizhi {
			flex-shrink: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 90upx;
			color: #888;
			font-size: 44upx;
		}

		.cen {
			display: flex;
			flex-direction: column;
			flex: 1;
			font-size: 28upx;
			color: $font-color-dark;
		}

		.name {
			font-size: 34upx;
			margin-right: 24upx;
		}

		.address {
			margin-top: 16upx;
			margin-right: 20upx;
			color: $font-color-light;
		}

		.icon-you {
			font-size: 32upx;
			color: $font-color-light;
			margin-right: 30upx;
		}

		.a-bg {
			position: absolute;
			left: 0;
			bottom: 0;
			display: block;
			width: 100%;
			height: 5upx;
		}
	}

	.goods-section {
		margin-top: 16upx;
		background: #fff;
		padding-bottom: 1px;

		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}

		.logo {
			margin: 0;
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}

		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}

		.g-item {
			display: flex;
			margin: 20upx 30upx;

			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}

			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}

			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}

			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}

			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;

				.price {
					margin-bottom: 4upx;
				}
				.number{
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
			}

			.step-box {
				position: relative;
			}
		}
	}
	.yt-list {
		margin-top: 16upx;
		background: #fff;
	}

	.yt-list-cell {
		display: flex;
		align-items: center;
		padding: 10upx 30upx 10upx 40upx;
		line-height: 70upx;
		position: relative;

		&.cell-hover {
			background: #fafafa;
		}

		&.b-b:after {
			left: 30upx;
		}

		.cell-icon {
			height: 32upx;
			width: 32upx;
			font-size: 22upx;
			color: #fff;
			text-align: center;
			line-height: 32upx;
			background: #f85e52;
			border-radius: 4upx;
			margin-right: 12upx;

			&.hb {
				background: #ffaa0e;
			}

			&.lpk {
				background: #3ab54a;
			}

		}

		.cell-more {
			align-self: center;
			font-size: 24upx;
			color: $font-color-light;
			margin-left: 8upx;
			margin-right: -10upx;
		}

		.cell-tit {
			flex: 1;
			font-size: 26upx;
			color: $font-color-light;
			margin-right: 10upx;
		}

		.cell-tip {
			font-size: 26upx;
			color: $font-color-dark;

			&.disabled {
				color: $font-color-light;
			}

			&.active {
				color: $base-color;
			}
			&.red{
				color: $base-color;
			}
		}

		&.desc-cell {
			.cell-tit {
				max-width: 90upx;
			}
		}

		.desc {
			flex: 1;
			font-size: $font-base;
			color: $font-color-dark;
		}
	}
	
	/* 支付列表 */
	.pay-list{
		padding-left: 40upx;
		margin-top: 16upx;
		background: #fff;
		.pay-item{
			display: flex;
			align-items: center;
			padding-right: 20upx;
			line-height: 1;
			height: 110upx;	
			position: relative;
		}
		.icon-weixinzhifu{
			width: 80upx;
			font-size: 40upx;
			color: #6BCC03;
		}
		.icon-alipay{
			width: 80upx;
			font-size: 40upx;
			color: #06B4FD;
		}
		.icon-xuanzhong2{
			display: flex;
			align-items: center;
			justify-content: center;
			width: 60upx;
			height: 60upx;
			font-size: 40upx;
			color: $base-color;
		}
		.tit{
			font-size: 32upx;
			color: $font-color-dark;
			flex: 1;
		}
	}
	
	.footer{
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 995;
		display: flex;
		align-items: center;
		width: 100%;
		height: 90upx;
		justify-content: space-between;
		font-size: 30upx;
		background-color: #fff;
		z-index: 998;
		color: $font-color-base;
		box-shadow: 0 -1px 5px rgba(0,0,0,.1);
		.price-content{
			padding-left: 30upx;
		}
		.price-tip{
			color: $base-color;
			margin-left: 8upx;
		}
		.price{
			font-size: 36upx;
			color: $base-color;
		}
		.submit{
			display:flex;
			align-items:center;
			justify-content: center;
			width: 280upx;
			height: 100%;
			color: #fff;
			font-size: 32upx;
			background-color: $base-color;
		}
	}
	
	/* 优惠券面板 */
	.mask{
		display: flex;
		align-items: flex-end;
		position: fixed;
		left: 0;
		top: var(--window-top);
		bottom: 0;
		width: 100%;
		background: rgba(0,0,0,0);
		z-index: 9995;
		transition: .3s;
		
		.mask-content{
			width: 100%;
			min-height: 30vh;
			max-height: 70vh;
			background: #f3f3f3;
			transform: translateY(100%);
			transition: .3s;
			overflow-y:scroll;
		}
		&.none{
			display: none;
		}
		&.show{
			background: rgba(0,0,0,.4);
			
			.mask-content{
				transform: translateY(0);
			}
		}
	}

	/* 优惠券列表 */
	.coupon-item{
		display: flex;
		flex-direction: column;
		margin: 20upx 24upx;
		background: #fff;
		.con{
			display: flex;
			align-items: center;
			position: relative;
			height: 120upx;
			padding: 0 30upx;
			&:after{
				position: absolute;
				left: 0;
				bottom: 0;
				content: '';
				width: 100%;
				height: 0;
				border-bottom: 1px dashed #f3f3f3;
				transform: scaleY(50%);
			}
		}
		.left{
			display: flex;
			flex-direction: column;
			justify-content: center;
			flex: 1;
			overflow: hidden;
			height: 100upx;
		}
		.title{
			font-size: 32upx;
			color: $font-color-dark;
			margin-bottom: 10upx;
		}
		.time{
			font-size: 24upx;
			color: $font-color-light;
		}
		.right{
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			font-size: 26upx;
			color: $font-color-base;
			height: 100upx;
		}
		.price{
			font-size: 44upx;
			color: $base-color;
			&:before{
				content: '￥';
				font-size: 34upx;
			}
		}
		.tips{
			font-size: 24upx;
			color: $font-color-light;
			line-height: 60upx;
			padding-left: 30upx;
		}
		.circle{
			position: absolute;
			left: -6upx;
			bottom: -10upx;
			z-index: 10;
			width: 20upx;
			height: 20upx;
			background: #f3f3f3;
			border-radius: 100px;
			&.r{
				left: auto;
				right: -6upx;
			}
		}
	}

</style>
